<template>
  <div class="table">
    <el-table :data="dataList" border style="width: 100%" @sort-change="handleSortChange" height="600">
      <el-table-column fixed prop="id" label="ID" min-width="80" sortable="custom">
      </el-table-column>
      <el-table-column label="icon" width="80">
        <template slot-scope="scope">
          <img style="width: 50px;height:50px;margin:auto" :src="scope.row.iconUrl" alt="">
        </template>
      </el-table-column>
      <el-table-column label="名称" prop="name" min-width="180"></el-table-column>
      <el-table-column prop="versionCode" label="版本号" min-width="100">
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="120">
        <template slot-scope="scope">
          <!-- 0-草稿 1-待审核 2-待上架 3-驳回 4-已上架 5-已下架 6-已禁用 -->
          <span v-if="scope.row.status===0" style="color:gray">草稿</span>
          <span v-if="scope.row.status===1" style="color:Orange">待审核</span>
          <span v-if="scope.row.status===2" style="color:Orange">待上架</span>
          <span v-if="scope.row.status===3" style="color:Orange">驳回</span>
          <span v-if="scope.row.status===4" style="color:green">已上架</span>
          <span v-if="scope.row.status===5" style="color:black">已下架</span>
          <span v-if="scope.row.status===6" style="color:red">已禁用</span>
        </template>
      </el-table-column>
      <el-table-column prop="startTime" label="开始时间" min-width="150" sortable="custom">
      </el-table-column>
      <el-table-column prop="endTime" label="结束时间" min-width="150" sortable="custom">
      </el-table-column>
      <el-table-column prop="modifier" label="上次修改人" min-width="100">
      </el-table-column>
      <el-table-column label="操作" min-width="270">
        <template slot-scope="scope">
          <el-button @click="goCheckPage(scope.row)" size="mini" type="primary">审核</el-button>
          <el-button @click="previewTest(scope.row)" size="mini" type="primary">预览测试</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="输入测试IMEI" :visible.sync="testDialogVisible" width="600px" center>
      <div class="test-dialog">
        <span>输入1-20个IMEI码</span>
        <div style="display: flex;margin:20px">
        <el-input v-model="imeiTestValue" placeholder="请输入IMEI" clearable style="width:200px;margin-right: 20px;"></el-input>
        <el-button type="primary" size="mini" @click="saveIMEI()">添加</el-button>
        </div>
        <div style="display:flex;width:100%;padding:10px 20px;flex-wrap: wrap">
          <el-tag v-for="(imei, index) in imeiList" :key="index" closable type="info" @close="deleteIMEI(index)" style="margin-right: 10px;margin-bottom: 10px">
            {{imei}}
          </el-tag>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="testDialogVisible=false">取 消</el-button>
        <el-button type="primary" @click="testBtn()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "table-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    dataList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      testData: {},
      imeiTestValue: "",
      imeiList: [],
      testDialogVisible: false,
    };
  },
  methods: {
    saveIMEI(){
      if (this.imeiList.length > 19) {
        this.$confirm("只能输入1-20个IMEI", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiTestValue)) {
        this.$confirm("IMEI码重复！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiTestValue)) {
        this.imeiList.push(this.imeiTestValue);
        // this.imeiTestValue = "";
      } else {
        this.$confirm("IMEI码不符合要求！(15位数字)", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    testBtn() {
      if(this.imeiList.length<1){
        this.$confirm("请至少填入一个IMEI码", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false
      }
      this.uploadLoading = true;
      let param = {
        status: this.testData.status,
        name: this.testData.name,
        startTime: this.testData.startTime,
        endTime: this.testData.endTime,
        priority: this.testData.priority,
        iconUrl: this.testData.iconUrl,
        displayText: this.testData.displayText,
        textLink: this.testData.textLink,
        buttonName: this.testData.buttonName,
        buttonLink: this.testData.buttonLink,
        notice: this.testData.notice,
        imeis: this.imeiList.join(';')
      };
      this.$http
        .post("/scene/previewTips", param)
        .then(response => {
          if(response.data.code===0){
            this.$message({
                  type: "success",
                  message: "预览推送成功!"
                });
            this.uploadLoading = false;
            this.testDialogVisible = false;
          }else {
            this.uploadLoading = false;
            this.testDialogVisible = false;
            this.$message.error('预览推送失败!');
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //预览测试操作
    previewTest(row) {
      this.testData = row;
      this.testDialogVisible = true;
    },
    //跳转数据浏览页面
    goDataPage(data) {
      this.$emit("table-event", {
        type: "data-page",
        data: data
      });
    },
    //跳转审核页面
    goCheckPage(data) {
      this.$emit("table-event", {
        type: "check-page",
        data: data
      });
    },
    //刷新数据
    refreshData() {
      this.$emit("table-event", {
        type: "refresh-data"
      });
    },
    //排序事件
    handleSortChange(data) {
      let field = data.prop;
      let order = "";
      if (data.order === "ascending") {
        order = "asc";
      } else if (data.order === "descending") {
        order = "desc";
      }
      this.$emit("table-event", {
        type: "sortChange",
        data: {
          field: field,
          order: order
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.test-dialog {
  margin-top: -30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.table {
  width: 100%;
  margin-top: 30px;
}
.shangjia-div {
  display: flex;
  flex-direction: column;
}
.text-line {
  display: flex;
  align-items: center;
}
.shangjia-date {
  margin-left: 7px;
}
.shangjia-text1 {
  color: rgb(192, 0, 0);
  margin-bottom: 20px;
}
.shangjia-text2 {
  font-size: 10px;
  color: rgb(196, 196, 196);
  margin-bottom: 20px;
}
</style>